/* end of number input handler */

.br-10_h-40 {
  border-radius: 10px !important;
  height: 40px !important;
  align-items: center !important;
}

.ant-drawer-title {
  font-family: 'kurdish_bold';
}

/*  */
html.rtl .ant-picker,
html.rtl .ant-picker-dropdown {
  font-family: 'Cuprumsemi';
}

html.rtl .ant-picker-input > input::placeholder {
  font-family: 'kurdish_regular';
}

.ant-table-measure-row {
  height: 0px;
  position: absolute;
}

.login_page {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.login_image {
  width: 400px;
}

.login_image img {
  width: 100%;
}

.phone_submit_btn {
  padding: 0;
  margin: 0;
  font-size: 16px;
  color: #fafafa;
  font-family: "SemiBold";
  background: #52c1c4;
  border-radius: 6px;
  height: 45px;
  border: none;
  cursor: pointer;
  @include transition-all-03s;
}

.phone_submit_btn:disabled {
  background: #c7ced5;
  cursor: not-allowed;
}

form.otp_form_login {
  height: 165px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-evenly;
  border-radius: 8px;
  border: thin solid #dbdbdd;
}

form.otp_form_login div {
  display: flex;
  width: 240px;
  align-items: center;
  justify-content: space-between;
}

form.otp_form_login div .code-input {
  padding: 0;
  margin: 0;
  width: 30px;
  height: 45px;
  border: none;
  text-align: center;
  border-bottom: 2px solid #7d7d7e47;
  font-size: 18px;
  color: #57636f;
  line-height: 17px;
  font-family: "Regular";
}

input[type="submit"] {
  margin: 0;
  padding: 0;
  width: 280px;
  height: 45px;
  border: none;
  color: #ffffff;
  border-radius: 8px;
  background-color: #52c1c4;
  cursor: pointer;
}

.resend_otp {
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #57636f;
  font-size: 14px;
}

button.ant-btn-primary{
  background-color: #4fc2c5;
}

.ant-carousel .slick-dots li.slick-active button{
  background-color: #4fc2c5 !important;
}

.ant-carousel .slick-dots li button{
  background-color: #005ac5 !important;
}
