.logo {
  padding: 13px 0;
  text-align: center;
  margin-bottom: 16px;
}

.logo_small {
  padding: 20px 0;
  text-align: center;
  margin-bottom: 16px;
}

.sider_toggler_btn {
  position: absolute;
  top: 0;
  left: 8px;
  z-index: 2;
}

.main_header_container {
  width: calc(100% - 50px);
  height: 100%;
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.section_title {
  font-size: 18px;
  margin-left: 20px;
}

html.rtl .section_title {
  font-family: 'kurdish_bold';
}

.header_avatar {
  width: calc(100% - 250px);
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 16px;
}

.header_avatar > .bg-img {
  width: 35px;
  height: 35px;
}

.header_display_name {
  color: #1e2832;
  font-size: 14px;
  font-family: 'Speda-Bold';
  text-transform: capitalize;
}

/* sider scroll styles */
.layout_sider::-webkit-scrollbar {
  width: 3px;
}

/* Track */
.layout_sider::-webkit-scrollbar-track {
  background: #fff;
  display: none;
}

/* Handle */
.layout_sider::-webkit-scrollbar-thumb {
  background: #c1c1c1;
}
/* end of sider scroll styles */

/* notification in sider bar */
.ant-scroll-number-only > p.ant-scroll-number-only-unit {
  color: #fff;
}
